@import "https://fonts.googleapis.com/css2?family=Muli:ital,wght@0,700;1,400&display=swap";
:root {
  --line-border-fill: #3498db;
  --line-border-empty: #e0e0e0;
}

* {
  box-sizing: border-box;
}

body {
  height: 100vh;
  background-color: #f6f7fb;
  justify-content: center;
  align-items: center;
  margin: 0;
  font-family: Muli, sans-serif;
  display: flex;
  overflow: hidden;
}

.container {
  text-align: center;
}

.progress-container {
  max-width: 100%;
  width: 350px;
  justify-content: space-between;
  margin-bottom: 30px;
  display: flex;
  position: relative;
}

.progress-container:before {
  content: "";
  background-color: var(--line-border-empty);
  height: 4px;
  width: 100%;
  z-index: -1;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.progress {
  background-color: var(--line-border-fill);
  height: 4px;
  width: 0%;
  z-index: -1;
  transition: all .4s;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.circle {
  color: #999;
  height: 30px;
  width: 30px;
  border: 3px solid var(--line-border-empty);
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: all .4s;
  display: flex;
}

.circle.active {
  border-color: var(--line-border-fill);
}

.btn {
  background-color: var(--line-border-fill);
  color: #fff;
  cursor: pointer;
  border: 0;
  border-radius: 6px;
  margin: 5px;
  padding: 8px 30px;
  font-family: inherit;
  font-size: 14px;
}

.btn.active {
  transform: scale(.98);
}

.btn:focus {
  outline: 0;
}

.btn:disabled {
  background-color: var(--line-border-empty);
  cursor: not-allowed;
}

